/* client/src/scss/components/_pagination.scss */
/* client/src/scss/comopnents/_loading.scss */
.loader {
  position: relative;
  margin: 8px auto;
  width: 100px; }
  .loader:before {
    content: '';
    display: block;
    padding-top: 100%; }

.circular {
  animation: rotate 2s linear infinite;
  height: 100%;
  transform-origin: center center;
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto; }

.path {
  stroke-dasharray: 1, 200;
  stroke-dashoffset: 0;
  animation: dash 1.5s ease-in-out infinite, color 6s ease-in-out infinite;
  stroke-linecap: round; }

@keyframes rotate {
  100% {
    transform: rotate(360deg); } }

@keyframes dash {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0; }
  50% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -35px; }
  100% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -124px; } }

@keyframes color {
  100%,
  0% {
    stroke: #2BBBAD; }
  40% {
    stroke: #2BBBAD; }
  66% {
    stroke: #2BBBAD; }
  80%,
  90% {
    stroke: #2BBBAD; } }

/* client/src/scss/components/_button.scss */
button.button {
  border-radius: 4px;
  color: white;
  background: #1565c0;
  padding: 8px calc(8px * 2);
