button.button:hover {
  cursor: pointer;
  transform: translateY(-2px);
  transition: all 0.3s ease; }

button.button:active, button.button:focus {
  border: none;
  background: #2BBBAD;
  transition: all 0.3s ease; }

button.button--danger {
  background: #ff4444; }

button.button--warning {
